:root {
  --color-dark-text: #494450;
  --color-text: #615c69;
  --color-light-text: #d2bea6;
  --font-bebas: 'Bebas Neue', Arial, Helvetica, sans-serif;
  --bg-body: #f1e2cf;
  --bg-light: #e8d7c3;
  --bg-dark: #494450;
}

@font-face {
  font-family: 'Bebas Neue';
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/BebasNeueBold.woff2) format('woff2'), url(../fonts/BebasNeueBold.woff) format('woff');
}

@font-face {
  font-family: 'Montserrat';
  font-weight: 900;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/Montserrat-Black.woff2) format('woff2'), url(../fonts/Montserrat-Black.woff) format('woff');
}

@font-face {
  font-family: 'Montserrat';
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/Montserrat-Medium.woff2) format('woff2'), url(../fonts/Montserrat-Medium.woff) format('woff');
}

html {
  height: 100%;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  font-weight: 500;
  font-size: 13px;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  color: var(--color-text);
  background-color: var(--bg-body);
}

abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
  border-bottom: none;
}

b,
strong {
  font-weight: bolder;
}

small {
  font-size: 80%;
}

img {
  max-width: 100%;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-size: 100%;
  line-height: 1.15;
  font-family: inherit;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  display: table;
  box-sizing: border-box;
  max-width: 100%;
  padding: 0;
  color: inherit;
  white-space: normal;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: textfield;
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

details {
  display: block;
}

summary {
  display: list-item;
}

.visually-hidden {
  position: absolute;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border: none;
}

.col {
  min-width: 320px;
  box-sizing: border-box;
  padding-right: 30px;
  padding-left: 30px;
}

.btn {
  display: inline-block;
  padding: 15px 50px 12px;
  font-weight: 700;
  font-size: 22px;
  line-height: 1;
  font-family: var(--font-bebas);
  text-align: center;
  color: var(--color-light-text);
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2.5px;
  background-color: var(--bg-body);
  border: 0;
  border: 1px solid #d1bda5;
  border-radius: 26px;
  transition: box-shadow 0.3s ease-in-out;
  appearance: none;
}

.btn:hover {
  box-shadow: 0px 4px 20px 0px var(--color-light-text);
}

.btn:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #d1bda5;
  outline-offset: -1px;
  box-shadow: 0px 2px 4px 0px #3a2b4f;
}

.btn:active {
  box-shadow: 0px 2px 4px 0px transparent;
}

.btn--success {
  color: var(--color-light-text);
  background-color: var(--bg-dark);
  border: 1px solid var(--bg-dark);
  box-shadow: 0px 4px 16px 0px #3a2b4f;
  transition: box-shadow 0.3s ease-in-out;
}

.btn--success:hover {
  box-shadow: 0px 2px 4px 0px #3a2b4f;
}

.btn--success:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #d1bda5;
  outline-offset: -1px;
}

.btn--success:active {
  box-shadow: 0px 2px 4px 0px transparent;
}

.btn--text-small {
  letter-spacing: 1px;
}

.btn:before {
  content: "";
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.btn {
  display: inline-block;
  vertical-align: middle;
}

.btn:disabled {
  opacity: 0.6;
  box-shadow: none;
}

.form ::placeholder {
  color: var(--color-light-text);
}

.form :disabled,
.form :hover:disabled,
.form :focus:disabled,
.form :active:disabled {
  background-color: var(--bg-body);
  border-color: var(--bg-light);
  box-shadow: none;
  opacity: 0.7;
}

.form__fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

.form__legend {
  font-weight: 700;
  font-size: 22px;
  line-height: 45px;
  font-family: var(--font-bebas);
  color: var(--color-light-text);
  letter-spacing: 3px;
}

.form__grid .form__label {
  grid-row-end: 2;
  grid-row-start: 1;
}

.form__label {
  display: block;
  font-weight: 500;
  font-size: 13px;
  line-height: 20px;
  color: var(--color-dark-text);
}

.form__control {
  box-sizing: border-box;
  width: 100%;
  margin-bottom: 24px;
  padding: 16px 20px;
  font-weight: 900;
  font-size: 14px;
  line-height: 1;
  color: var(--color-dark-text);
  letter-spacing: 0.5px;
  background-color: var(--bg-light);
  border: 0;
  border: 1px solid var(--bg-light);
  border-radius: 6px;
  transition: box-shadow 0.2s ease-in-out;
}

.form__control:hover {
  box-shadow: 0px 4px 20px 0px var(--color-light-text);
}

.form__control:focus-visible {
  outline: none;
  box-shadow: 0px 4px 20px 0px var(--color-light-text);
}

.form__control:active {
  outline-color: transparent;
}

.form__radio-wrap {
  display: flex;
  margin-bottom: 24px;
}

.form__radio-label {
  flex-grow: 1;
  box-sizing: border-box;
  padding: 17px 11px;
  font-weight: 900;
  font-size: 14px;
  line-height: 1;
  text-align: center;
  color: var(--color-light-text);
  border: 1px solid var(--bg-light);
  transition: box-shadow 0.2s ease-in-out;
}

.form__radio-label:hover {
  box-shadow: 0px 4px 20px 0px var(--color-light-text);
}

.form__radio-label:first-of-type {
  border-radius: 8px 0 0 8px;
}

.form__radio-label:last-of-type {
  border-radius: 0 8px 8px 0;
}

.form__radio {
  appearance: none;
}

.form__radio:disabled + .form__radio-label {
  opacity: 0.7;
}

.form__radio:disabled + .form__radio-label:hover {
  box-shadow: 0 0 0 0 transparent;
}

.form__radio:focus-visible + .form__radio-label {
  position: relative;
  box-shadow: 0px 4px 20px 0px var(--color-light-text);
}

.form__radio:checked + label {
  color: var(--color-dark-text);
  border-color: var(--bg-dark);
}

select.form__control {
  background-image: url(../img/select.svg);
  background-repeat: no-repeat;
  background-position: right 10px center;
  appearance: none;
}

.form__select-icon {
  position: relative;
}

.form__select-icon select {
  padding-right: 40px;
  text-indent: 40px;
}

.form__select-icon option {
  text-indent: 0;
  padding-top: 16px;
  padding-bottom: 16px;
}

.form__select-icon::before {
  content: '';
  position: absolute;
  top: 13px;
  left: 16px;
  width: 30px;
  height: 24px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
}

.form__select-icon[data-icon='am']::before {
  background-image: url(../img/flags/am.svg);
}

.form__select-icon[data-icon='az']::before {
  background-image: url(../img/flags/az.svg);
}

.form__select-icon[data-icon='by']::before {
  background-image: url(../img/flags/by.svg);
}

.form__select-icon[data-icon='ba']::before {
  background-image: url(../img/flags/ba.svg);
}

.form__select-icon[data-icon='ee']::before {
  background-image: url(../img/flags/ee.svg);
}

.form__select-icon[data-icon='ge']::before {
  background-image: url(../img/flags/ge.svg);
}

.form__select-icon[data-icon='kz']::before {
  background-image: url(../img/flags/kz.svg);
}

.form__select-icon[data-icon='kg']::before {
  background-image: url(../img/flags/kg.svg);
}

.form__select-icon[data-icon='lv']::before {
  background-image: url(../img/flags/lv.svg);
}

.form__select-icon[data-icon='lt']::before {
  background-image: url(../img/flags/lt.svg);
}

.form__select-icon[data-icon='md']::before {
  background-image: url(../img/flags/md.svg);
}

.form__select-icon[data-icon='ru']::before {
  background-image: url(../img/flags/ru.svg);
}

.form__select-icon[data-icon='tj']::before {
  background-image: url(../img/flags/tj.svg);
}

.form__select-icon[data-icon='tm']::before {
  background-image: url(../img/flags/tm.svg);
}

.form__select-icon[data-icon='ua']::before {
  background-image: url(../img/flags/ua.svg);
}

.form__select-icon[data-icon='uz']::before {
  background-image: url(../img/flags/uz.svg);
}

.form__password {
  position: relative;
}

.form__password input {
  padding-right: 50px;
}

.form__password:focus-within input,
.form__password-toggle:hover ~ input {
  box-shadow: 0px 4px 20px 0px var(--color-light-text);
}

.form__password-toggle {
  position: absolute;
  right: 0;
  width: 50px;
  height: 50px;
  background-color: transparent;
  border: 0;
  box-shadow: none;
}

.form__password-toggle::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 24px;
  height: 24px;
  background-image: url(../img/form/password-hide.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transform: translate(-50%, -50%);
}

.form__password-toggle--visible::before {
  background-image: url(../img/form/password-visible.svg);
}

.form__checkbox-wrap p {
  padding-left: 60px;
}

.form__checkbox {
  outline: none;
}

.form__checkbox-label {
  display: flex;
  align-items: center;
  font-weight: 900;
  font-size: 14px;
  line-height: 20px;
  color: var(--color-dark-text);
}

.form__checkbox-view {
  position: relative;
  display: block;
  flex-shrink: 0;
  width: 50px;
  height: 50px;
  margin-right: 10px;
  background-color: var(--bg-dark);
  border-radius: 8px;
  transition: box-shadow 0.2s ease-in-out;
}

.form__checkbox-view::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 18px;
  height: 12px;
  transform: translate(-50%, -50%);
}

.form__checkbox-label:hover .form__checkbox-view,
.form__checkbox-view:hover {
  box-shadow: 0 4px 20px 0 var(--color-light-text);
}

.form__checkbox:checked + .form__checkbox-view::before {
  background-image: url(../img/form/icon_selected.svg);
  background-size: contain;
}

.form__checkbox:focus-visible .form__checkbox-view {
  outline-width: 2px;
  outline-style: double;
  outline-color: #d1bda5;
  outline-offset: -1px;
  box-shadow: 0px 2px 4px 0px #3a2b4f;
}

.form__checkbox:disabled + .form__checkbox-view {
  box-shadow: none;
  opacity: 0.6;
}

.form__notes {
  line-height: 20px;
}

.form__number-wrap {
  display: flex;
}

.form__number-wrap ::-webkit-outer-spin-button,
.form__number-wrap ::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.form__number-wrap input {
  appearance: textfield;
}

.form__button-minus,
.form__button-plus {
  position: relative;
  width: 70px;
}

.form__button-minus {
  border-radius: 6px 0 0 6px;
}

.form__button-minus::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 4px;
  background-color: var(--color-light-text);
}

.form__button-plus {
  border-radius: 0 6px 6px 0;
}

.form__button-plus::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 4px;
  background-color: var(--color-light-text);
}

.form__button-plus::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 4px;
  height: 20px;
  background-color: var(--color-light-text);
}

.form__number {
  text-align: center;
  border-radius: 0;
  margin-left: 2px;
  margin-right: 2px;
}

body {
  align-items: center;
  padding: 50px 30px;
}

.blank {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 520px;
}

.form {
  width: 100%;
}

@media (min-width: 576px) {
  .hidde-min-sm {
    display: none;
  }

  .form__control {
    font-size: 16px;
  }
}

@media (min-width: 768px) {
  .hidde-min-md {
    display: none;
  }

  .col {
    padding-right: 40px;
    padding-left: 40px;
  }

  .form__grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 0 20px;
  }
}

@media (min-width: 990px) {
  .hidde-min-lg {
    display: none;
  }
}

@media (min-width: 1200px) {
  .hidde-min-xl {
    display: none;
  }
}

@media (min-width: 1620px) {
  .hidde-min-xxl {
    display: none;
  }
}

@media (max-width: 1619px) {
  .hidde-max-xxl {
    display: none;
  }
}

@media (max-width: 1199px) {
  .hidde-max-xl {
    display: none;
  }
}

@media (max-width: 989px) {
  .hidde-max-lg {
    display: none;
  }
}

@media (max-width: 767px) {
  .hidde-max-md {
    display: none;
  }
}

@media (max-width: 575px) {
  .hidde-max-sm {
    display: none;
  }
}